import { HvContainer } from "@hitachivantara/uikit-react-core";
import useNavigationContext from "../../../providers/hooks/useNavigationContext";
import VerticalNavigation from "../VerticalNavigation";

import { StyledMain, StyledContainer } from "./styles";
import useLayoutContext from "../../../providers/hooks/useLayoutContext";

import { useAppShellConfig } from "../../../lib/hooks";

export type MainProps = {
  children: React.ReactNode;
};

const Main = ({ children }: MainProps) => {
  const {
    hasVerticalNavigation,
    verticalNavigationMode,
    showHeaderSubMenu,
    isCompactMode,
    switchVerticalNavigationMode
  } = useNavigationContext();
  const { bannerMaxHeight } = useLayoutContext();
  const { mainPanel } = useAppShellConfig();

  return (
    <StyledContainer
      showHeaderSubMenu={showHeaderSubMenu}
      isCompactMode={isCompactMode}>
      {hasVerticalNavigation && verticalNavigationMode !== "CLOSED" && (
        <VerticalNavigation
          onClickAway={isCompactMode ? switchVerticalNavigationMode : undefined}
        />
      )}

      <StyledMain paddingTop={bannerMaxHeight}>
        <HvContainer maxWidth={mainPanel?.maxWidth ?? "xl"}>
          {children}
        </HvContainer>
      </StyledMain>
    </StyledContainer>
  );
};

export default Main;
